import React from 'react';
import styled from 'styled-components';
import IconDown from './IconDown';
import AppText from '../AppText';
import AppTextName from '../AppTextName';
import AppTextProf from '../AppTextProf';
/* import ThreeEffect from './Three/TheeeEffect'; */
const SectionHome = () => {
  return (
    <SectionHomeWrapper>
      <section className="main" id="home">
        <AppText />
        <AppTextName />
        <AppTextProf />
        <IconDown />
      </section>
    </SectionHomeWrapper>
  );
};

const SectionHomeWrapper = styled.div`
  .main {
    position: relative;
  }

  #home {
    height: 100vh;
    background-color: #282c34;
  }
`;

export default SectionHome;
